.main {
  width: 350px;
  height: 600px;
  border: 1px solid black;
  border-radius: 10px;
  background-size: 100%;
  background-color: red;
  margin: 80px auto;
  background-image: url(https://images.unsplash.com/photo-1562853039-45426cece82b?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2411&q=80);
  background-size: cover;
}

.cont {
  display: flex;
  justify-content: center;
  position: relative;
  top: 30px;
}
.form {
  display: flex;
}
input {
  background: #efe8e8;
  width: 200px;
  height: 30px;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 3px;
}
.btn {
  height: 30px;
  background: #ddcece;
  border: none;
}
.box {
  color: white;
  height: 450px;
  position: relative;
  top: 8%;
  width: 90%;
  margin: auto;
  background-color: rgba(68, 68, 65, 0.9);
  opacity: 0.7;
  border-radius: 20px;
}
.title {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  color: white;
  opacity: none;
}
.context {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  align-items: center;
  text-align: center;
}
.icon {
  text-align: center;
}
.temp {
  font-size: 40px;
  padding-top: 2px;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 36px;
    line-height: 50px;
  }
  .form {
    display: inline-block;
  }
  .box {
    color: white;
    height: 440px;
    position: relative;
    top: 4%;
    width: 90%;
    margin: auto;
    background-color: rgba(68, 68, 65, 0.9);
    opacity: 0.7;
    border-radius: 20px;
  }
  .btn_cont {
    display: flex;
    justify-content: center;
    margin-top: 5px;
  }
  .btn {
    color: blue;
    max-width: 100px;
    background-color: aliceblue;
  }
}
